import {
  AfterViewInit,
  Component, ElementRef,
  EventEmitter, HostListener,
  Input,
  OnChanges,
  Output,
  SimpleChanges,
  TemplateRef, ViewChild,
} from '@angular/core';
import { slideAlertMotion, InputBoolean, NgClassType } from 'ng-zorro-antd/core';
import {FormControl} from "@angular/forms";

@Component({
  selector: 'cmacs-open-textarea',
  exportAs: 'cmacsOpenTextarea',
  animations: [slideAlertMotion],
  templateUrl: './cmacs-open-textarea.component.html',
  preserveWhitespaces: false,
  styleUrls: ['./cmacs-open-textarea.component.css'],
  styles: [
    `
      cmacs-open-textarea {
        display: block;
      }
    `
  ]
})
export class CmacsOpenTextareaComponent implements AfterViewInit {

  @Input() formControlCustom: FormControl = new FormControl('', []);
  @Input() width: number = 250;
  @Input() placeholder: string = '';
  allowEdition = false;
  enableDivider = true;
  @ViewChild('textarea', {read: ElementRef}) textarea: ElementRef;

  constructor() { }

  @HostListener('window:click', ['$event'])
  onClick(event: Event) {
    if (this.textarea && event.target !== this.textarea.nativeElement) {
      this.allowEdition = false;
      this.enableDivider = false;
    }
  }

  isEnabled() {
    if (this.allowEdition || !this.formControlCustom.value) {
      this.allowEdition = true;
      return true;
    }
    this.allowEdition = false;
    return false;
  }

  isTextEnabled() {
    return !this.allowEdition && this.formControlCustom.value;
  }

  startEdition($event: Event) {
    $event.preventDefault();
    $event.stopImmediatePropagation();
    this.allowEdition = true;
  }

  ngAfterViewInit() {
    this.enableDivider = this.allowEdition;
  }
}
