import { Component, OnInit, Input, OnChanges } from '@angular/core';
import { WidgetActionType } from '../core/enums/widget-action-type.enum';
import { WidgetDataType } from '../core/enums/widget-type.enum';

@Component({
  selector: 'cmacs-general-chart',
  templateUrl: './cmacs-general-chart.component.html',
  styleUrls: ['./cmacs-general-chart.component.css']
})
export class CmacsGeneralChartComponent implements OnInit {

  // content
  @Input() data: any[] = [];

  // options
  @Input() chartSelected: WidgetActionType = WidgetActionType.verticalBarChart;
  @Input() dataType = WidgetDataType.single;
  @Input() showXAxis = true;
  @Input() showXAxisLabel = false;
  @Input() xAxisLabel = '';
  @Input() showYAxis = true;
  @Input() showYAxisLabel = false;
  @Input() yAxisLabel = '';
  @Input() showLegend = true;
  @Input() legendTitle = '';

  //
  @Input() view!: number[];
  @Input() colorScheme = {
    domain: ['#5AA454', '#C7B42C', '#AAAAAA']
  };

  legendPosition = 'below';
  WidgetActionTypeEnum = WidgetActionType;
  WidgetDataTypeEnum = WidgetDataType;

  constructor() { }

  ngOnInit() {
  }

  menuClick(type: WidgetActionType): void {
    if (this.isChartTypeMenu(type)) {
      this.chartSelected = type;
    }
  }

  isChartTypeMenu(type: WidgetActionType): boolean {
    return type === WidgetActionType.verticalBarChart || type === WidgetActionType.horizontalBarChart
      || type === WidgetActionType.pieChart || type === WidgetActionType.lineChart || type === WidgetActionType.gauge;
  }

}
