import { Component, OnInit, Input, AfterViewInit, Output, EventEmitter, OnChanges } from '@angular/core';
import { WidgetActionType } from '../core/enums/widget-action-type.enum';
import { ChartDataMulti } from '../core/interfaces/chart-data-interface';
import { UtilService } from '../core/services/util.service';

@Component({
  selector: 'cmacs-normalized-horizontal-bar-chart',
  templateUrl: './cmacs-normalized-horizontal-bar-chart.component.html',
  styleUrls: ['./cmacs-normalized-horizontal-bar-chart.component.css']
})
export class CmacsNormalizedHorizontalBarChartComponent implements OnInit, OnChanges {

  @Output() clickMenu = new EventEmitter<any>();

  // content
  @Input() data!: ChartDataMulti[];
  @Input() view!: number[];
  minWidth = 150;
  width = 300;
  pw = 1;

  // otpions
  @Input() colorScheme = {
    domain: ['#5AA454', '#C7B42C', '#AAAAAA']
  };

  id;

  legend!: string[];

  constructor(
    private util: UtilService
  ) {
    this.id = util.uuidv4();
  }

  ngOnInit() {
    let temp: any[] = [];
    this.data.map(d => d.series.map(x => x.name)).forEach((x) => temp = [...temp, ...x]);
    this.legend  = Array.from(new Set([...temp]));
  }

  ngOnChanges() {
    this.pw = 1;
    if (this.view && this.view.length === 2) {
      this.pw = this.view[0] > this.width ? this.view[0] / this.width : 1;
    }

    setTimeout(() => {
      for (let index = 0; index < this.data.length; index++) {
        const canvas = document.getElementById('canvas-' + this.id + '-' + index)  as HTMLCanvasElement;
        if (canvas.getContext) {
          //
          const context = canvas.getContext('2d');
          canvas.width = this.minWidth * this.pw;
          canvas.height = 20;
          const total = this.data[index].series.map(s => s.value).reduce((a, b) => a + b, 0);
          let move = 0;
          //
          const elems = this.data[index].series.sort((a, b) =>
            this.legend.findIndex(y => y === a.name) - this.legend.findIndex(y => y === b.name)
          );
          for (let j = 0; j < elems.length; j++) {
            // Reset the current path
            context.beginPath();
            context.moveTo(move, 0);
            context.lineWidth = 15;
            const val = elems[j].value;
            move += val / total * canvas.width;
            context.strokeStyle = this.colorScheme.domain[j];
            context.lineTo(move - 5, 0);
            context.stroke();
          }
        }
      }
    }, 0);
  }

  menuClick(type: WidgetActionType): void {
    this.clickMenu.emit(type);
  }

}
