import { Component, OnInit, Input, AfterViewInit, ChangeDetectionStrategy, Output, EventEmitter, OnChanges } from '@angular/core';
import { ChartDataMulti } from '../core/interfaces/chart-data-interface';
import { UtilService } from '../core/services/util.service';
import { WidgetActionType } from '../core/enums/widget-action-type.enum';

@Component({
  selector: 'cmacs-normalized-horizontal-bar-grouped',
  templateUrl: './cmacs-normalized-horizontal-bar-grouped.component.html',
  styleUrls: ['./cmacs-normalized-horizontal-bar-grouped.component.css'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class CmacsNormalizedHorizontalBarGroupedComponent implements OnInit, OnChanges {

  @Output() clickMenu = new EventEmitter<any>();

  // content
  @Input() data!: ChartDataMulti[];

  // otpions
  @Input() colorScheme = {
    domain: ['#5AA454', '#C7B42C', '#AAAAAA']
  };

  id;

  legend!: string[];
  selectList: string[];
  selectedValue!: string;

  // chart
  chartWidth = 250;
  chartHeight = 100;
  minWidth = 300;
  minHeight = 200;
  pw = 1;
  ph = 1;
  @Input() view!: number[];

  constructor(
    private util: UtilService
  ) {
    this.id = util.uuidv4();
  }

  ngOnInit() {
    // set legend values
    let temp: any[] = [];
    this.data.map(d => d.series.map(x => x.name)).forEach((x) => temp = [...temp, ...x]);
    this.legend  = Array.from(new Set([...temp]));

    // set select data
    this.selectList = this.data.map(x => x.name);
    if (this.selectList.length > 0) {
      this.selectedValue = this.selectList[0];
    }
  }

  ngOnChanges() {
    this.pw = 1;
    if (this.view && this.view.length === 2) {
      this.pw = this.view[0] > this.minWidth ? this.view[0] / this.minWidth : 1;
    }
    this.ph = 1;
    if (this.view && this.view.length === 2) {
      this.ph = this.view[1] > this.minHeight ? this.view[1] / this.minHeight : 1;
    }
    //
    setTimeout(() => {
      this.drawCanvas();
    }, 0);
  }

  menuClick(type: WidgetActionType): void {
    this.clickMenu.emit(type);
  }

  drawCanvas(): void {
    const canvas = document.getElementById('canvas-' + this.id)  as HTMLCanvasElement;
    if (canvas.getContext) {
      const context = canvas.getContext('2d');
      canvas.width = this.chartWidth * this.pw;
      canvas.height = this.chartHeight * this.ph;

      context.clearRect(0, 0, canvas.width, canvas.height);
      if (this.selectedValue) {
        const elems = this.data.find(x => x.name === this.selectedValue).series.sort((a, b) =>
          this.legend.findIndex(y => y === a.name) - this.legend.findIndex(y => y === b.name)
        );
        const total = elems.map(x => x.value).reduce((a, b) => a + b, 0);
        let move = 0;
        for (let i = 0; i < elems.length; i++) {
          context.beginPath();
          context.moveTo(move, canvas.height / 2);
          context.lineWidth = 8;
          const val = elems[i].value;
          move += val / total * canvas.width;
          context.strokeStyle = this.colorScheme.domain[i];
          context.lineTo(move - 5, canvas.height / 2);
          context.stroke();
        }
      }
    }
  }
}
