import { Component, OnInit, Input, ViewChild, ElementRef, AfterViewInit, OnChanges } from '@angular/core';
import { KPI, KPI_COLORS } from '../cmacs-kpi/cmacs-kpi.component';
import { DomSanitizer } from '@angular/platform-browser';

@Component({
  selector: 'cmacs-kpi-overview',
  templateUrl: './cmacs-kpioverview.component.html',
  styleUrls: ['./cmacs-kpioverview.component.css']
})
export class CmacsKPIOverviewComponent implements OnChanges {

  @Input() data: KPI[];
  @Input() title: string;
  @Input() view!: number[];
  minWidth = 300;
  fontChartNumber = 20;
  chartWidth = 84;
  p = 1;
  type = 'doughnut';
  @Input() colors: string[] = KPI_COLORS;

  @ViewChild('canvas', {read: ElementRef}) canvasRef: ElementRef;

  constructor(private sanitizer: DomSanitizer) { }

  ngOnChanges() {
    //
    this.p = 1;
    if (this.view && this.view.length === 2) {
      this.p = this.view[0] > this.minWidth ? this.view[0] / this.minWidth : 1;
    }

    setTimeout(() => {
      if (this.canvasRef) {
        const canvas = this.canvasRef.nativeElement as HTMLCanvasElement;
        canvas.width = this.chartWidth * this.p;
        canvas.height = this.chartWidth * this.p;
        const ctx = canvas.getContext('2d');

        let startAngle = 0;
        const data = this.getColoredData();

        if (this.getTotalCount() > 0) {
          for (const categ of data) {
            const val = categ.count;
            const sliceAngle = 2 * Math.PI * val / this.getTotalCount();

            this.drawPieSlice(
              ctx,
              canvas.width / 2,
              canvas.height / 2,
              Math.min(canvas.width / 2, canvas.height / 2),
              startAngle,
              startAngle + sliceAngle - 0.05,
              categ.color
            );
            startAngle += sliceAngle;
          }
        } else {
          this.drawPieSlice(
            ctx,
            canvas.width / 2,
            canvas.height / 2,
            Math.min(canvas.width / 2, canvas.height / 2),
            startAngle,
            startAngle + 2 * Math.PI,
            '#dee0e5'
          );
        }
        // drawing a white circle over the chart
        // to create the doughnut chart
        this.drawPieSlice(
          ctx,
          canvas.width / 2,
          canvas.height / 2,
          0.8 * Math.min(canvas.width / 2, canvas.height / 2),
          0,
          2 * Math.PI,
          '#ffffff'
        );
        // draw value
        ctx.font = this.fontChartNumber * this.p + 'px Roboto ';
        ctx.fillStyle = '#3b3f46';
        ctx.textAlign = 'center';
        ctx.fillText('' + this.getTotalCount(), canvas.width / 2, canvas.width / 2 + 5);
      }
    }, 50);
  }

  drawPieSlice(ctx: any, centerX: number, centerY: number, radius: number, startAngle: number, endAngle: number, color: any ){
    ctx.fillStyle = color;
    ctx.beginPath();
    ctx.moveTo(centerX, centerY);
    ctx.arc(centerX, centerY, radius, startAngle, endAngle);
    ctx.closePath();
    ctx.fill();
  }

  drawArc(ctx: any, centerX: number, centerY: number, radius: number, startAngle: number, endAngle: number) {
    ctx.beginPath();
    ctx.arc(centerX, centerY, radius, startAngle, endAngle);
    ctx.stroke();
  }

  sanitizeStyle(style: string) {
    return this.sanitizer.bypassSecurityTrustStyle(style);
  }

  getTotalCount() {
    let total = 0;
    this.data.forEach((item) => {
      total += item.count;
    });
    return total;
  }

  getWidth(count: number) {
    return count !== 0 ? Math.trunc(count * 100 / this.getTotalCount()) - 2 : 0;
  }

  getColoredData() {
    const coloredData: any = [];
    const remaining = this.data.length % this.colors.length;
    let rate = this.data.length / this.colors.length;
    if (remaining > 0) {
      rate = Math.trunc(rate) + 1;
    }

    let tempRate = rate;
    let opacity = 1;
    let colorIndex = 0;
    this.data.forEach((item) => {
      if (tempRate === 0) {
        tempRate = rate;
        colorIndex += 1;
        opacity = 1;
      }

      if (colorIndex >= this.colors.length) {
        colorIndex = 0;
      }

      if (opacity === 0.4) {
        opacity = 1;
      }

      coloredData.push({
        count: item.count,
        description: item.description,
        color: this.colors[colorIndex],
        opacity: opacity
      });

      opacity = opacity - 0.2;
      tempRate--;
    });

    return coloredData;
  }
}
