import { Component, OnInit, Output, EventEmitter, Input, AfterViewInit, ViewChild, TemplateRef, OnChanges } from '@angular/core';
import { UtilService } from '../core/services/util.service';
import { WidgetActionType } from '../core/enums/widget-action-type.enum';

@Component({
  selector: 'cmacs-status-distribution',
  templateUrl: './cmacs-status-distribution.component.html',
  styleUrls: ['./cmacs-status-distribution.component.css']
})
export class CmacsStatusDistributionComponent implements OnInit, AfterViewInit, OnChanges {

  @ViewChild('columnTemplate', { read: TemplateRef }) columnTemplate: TemplateRef<{}>;

  @Output() clickMenu = new EventEmitter<any>();

  // content
  @Input() view!: number[];
  @Input() data!: any[];
  @Input() columnsHeader: string[] = [];

  // chart
  col2 = 150;
  col3 = 70;
  minWidth = 300;
  chartWidth = 300;
  showChart = false;
  scrollY = 100;
  p = 1;
  scroll = {x: '300px', y: this.scrollY + 'px' };

  //
  id;
  dataTable: any[];
  configurationExpandableRows: any;

  constructor(
    private util: UtilService
  ) {
    this.id = util.uuidv4();
  }

  ngOnInit() {
  }

  ngOnChanges() {
    this.setScroll();
    //
    this.p = 1;
    if (this.view && this.view.length === 2) {
      this.p = this.view[0] > this.minWidth ? this.view[0] / this.minWidth : 1;
    }
    //
    this.showChart = false;

    setTimeout(() => {
      this.drawCanvas();
      this.setConfiguration();
      this.setData();
    }, 0);
    this.showChart = true;
  }

  ngAfterViewInit() {
  }

  setScroll() {
    if (this.view && this.view.length === 2) {
      this.scrollY = this.view[1] - 100;
    }
    this.scroll = {x: '300px', y: this.scrollY + 'px' };
  }

  menuClick(type: WidgetActionType): void {
    this.clickMenu.emit(type);
  }

  setConfiguration(): void {
    this.configurationExpandableRows = {
      fields: [
        {
          celdType: 3,
          display: '',
          property: 'color',
          width: '60px',
        },
        {
          celdType: 0,
          display: this.columnsHeader[0],
          property: 'name',
          editTemplate: 3,
          width: this.col2 * this.p + 'px',
        },
        {
          celdType: 0,
          display: this.columnsHeader[1],
          property: 'value',
          width: this.col3 * this.p + 'px',
          editTemplate: 2,
          editable: false
        }
      ],
      fieldId: 'key',
      fieldRate: 'fav'
    };
  }

  setData(): void {
    const temp: any[] = [];
    for (const item of this.data) {
      const newItem = {
        key: item.key,
        name: item.name,
        color: {
          ref: this.columnTemplate,
          context: {
            color: item.color
          }
        },
        value: item.value,
        children: []
      };
      for (let i = 0; i < item.rows.length; i++) {
        newItem.children.push({
          key: item.key + '-' + i,
          name: item.rows[i],
          color: { },
          value: ''
        });
      }
      temp.push(newItem);
    }
    this.dataTable = temp;
  }

  drawCanvas(): void {
    const canvas = document.getElementById('canvas-' + this.id)  as HTMLCanvasElement;
    canvas.height = 40;
    canvas.width = this.chartWidth * this.p;
    if (canvas.getContext) {
      const context = canvas.getContext('2d');
      const total = this.data.map(x => x.value).reduce((a, b) => a + b, 0);
      let move = 0;
      for (const item of this.data) {
        context.beginPath();
        context.moveTo(move, 1);
        context.lineWidth = 10;
        const val = item.value;
        move += val / total * canvas.width;
        context.strokeStyle = item.color;
        context.lineTo(move - 5, 1);
        context.stroke();
      }
    }
  }
}
